import * as React from "react"
import { useEffect } from "react"

import "react-responsive-carousel/lib/styles/carousel.min.css"
import Helmet from "react-helmet"
import { Link } from "../../.cache/gatsby-browser-entry"
import Logo from "../images/logo_full.svg"
import PartnerWS from "../images/partner_worldskills.svg"
import PartnerWSRT from "../images/partner_wsrt.svg"
import PartnerITL from "../images/partner_itl.svg"
import PartnerIISDF from "../images/partner_iisdf.svg"
import { Carousel } from "react-responsive-carousel"
import { fadeAnimationHandler } from "react-responsive-carousel/lib/js/components/Carousel/animations"

const Layout = ({ children, banners, showbanner = true, title }) => {
  if (banners) banners.sort((a, b) => 0.5 - Math.random())
  const headerRef = React.useRef()
  useEffect(() => {
    let h = document.documentElement,
      b = document.body,
      st = "scrollTop",
      sh = "scrollHeight",
      progress = document.querySelector("#progress"),
      scroll
    let scrollpos = window.scrollY
    let header = document.getElementById("header")

    if (!showbanner) {
      header.classList.add("shadow")
    }

    document.addEventListener("scroll", function () {
      /*Refresh scroll % width*/
      scroll = ((h[st] || b[st]) / ((h[sh] || b[sh]) - h.clientHeight)) * 100
      progress.style.setProperty("--scroll", scroll + "%")

      /*Apply classes for slide in bar*/
      scrollpos = window.scrollY

      if (!showbanner || scrollpos > 10) {
        header.classList.add("shadow")
      } else {
        header.classList.remove("shadow")
      }
    })

    //Javascript to toggle the menu
    document.getElementById("nav-toggle").onclick = function () {
      document.getElementById("nav-content").classList.toggle("hidden")
    }
  }, [showbanner])
  return (
    <>
      <Helmet>
        <html lang="ru" />
        <title>{title ?? "WorldSkills в IT-лицее КФУ"}</title>
        <body className="bg-white font-sans leading-normal tracking-normal" />
      </Helmet>
      <nav
        id="header"
        className="fixed w-full z-10 top-0 bg-white"
        ref={headerRef}
      >
        <div
          id="progress"
          className="h-1 z-20 top-0"
          style={{
            background:
              "linear-gradient(to right, var(--color-brand) var(--scroll), transparent 0)",
          }}
        />

        <div className="w-full md:max-w-4xl mx-auto flex flex-wrap items-center justify-between mt-0 py-3">
          <div className="flex mx-auto items-center justify-between gap-4">
            <div className="pl-4">
              <Link
                className="text-gray-900 text-base no-underline hover:no-underline font-extrabold text-xl"
                to="/"
              >
                <img
                  src={Logo}
                  alt="WorldSkills в IT-лицее КФУ"
                  className="w-full inline-block"
                />
              </Link>
            </div>

            <div className="block lg:hidden pr-4">
              <button
                id="nav-toggle"
                className="flex items-center px-3 py-2 border rounded text-gray-500 border-gray-600 hover:text-gray-900 hover:border-green-500 appearance-none focus:outline-none"
              >
                <svg
                  className="fill-current h-3 w-3"
                  viewBox="0 0 20 20"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <title>Меню</title>
                  <path d="M0 3h20v2H0V3zm0 6h20v2H0V9zm0 6h20v2H0v-2z" />
                </svg>
              </button>
            </div>
          </div>

          <div
            className="w-full bg-white flex-grow lg:flex lg:items-center lg:w-auto hidden lg:block mt-2 lg:mt-0 z-20"
            id="nav-content"
          >
            <ul className="list-reset lg:flex justify-end flex-1 items-center">
              <li className="mr-3">
                <Link
                  className="inline-block text-gray-600 no-underline hover:text-gray-900 hover:text-underline py-2 px-4"
                  to="/"
                >
                  Главная
                </Link>
              </li>
              <li className="mr-3">
                <Link
                  className="inline-block text-gray-600 no-underline hover:text-gray-900 hover:text-underline py-2 px-4"
                  to="/competitors"
                >
                  Сборная
                </Link>
              </li>
              <li className="mr-3">
                <Link
                  className="inline-block text-gray-600 no-underline hover:text-gray-900 hover:text-underline py-2 px-4"
                  to="/experts"
                >
                  Эксперты
                </Link>
              </li>
              <li className="mr-3">
                <a
                  className="inline-block text-gray-600 no-underline hover:text-gray-900 hover:text-underline py-2 px-4"
                  href="//vk.com/wsitl"
                  rel="noreferrer"
                  target="_blank"
                >
                  ВКонтакте
                </a>
              </li>
            </ul>
          </div>
        </div>
      </nav>

      {showbanner && (
        <div
          id="banner"
          className="w-full"
          style={{
            marginTop: headerRef.current ? headerRef.current.clientHeight : 74,
          }}
        >
          <Carousel
            autoPlay={true}
            infiniteLoop={true}
            showArrows={false}
            showStatus={false}
            showThumbs={false}
            showIndicators={false}
            stopOnHover={false}
            useKeyboardArrows={false}
            swipeable={false}
            animationHandler={fadeAnimationHandler}
            interval={7500}
          >
            {banners.map((image, i) => (
              <div className="banner-item w-full aspect-banner">
                <img
                  key={`banner_${i}`}
                  src={image.photo}
                  alt={"Баннер " + i}
                  style={
                    image.position ? { objectPosition: image.position } : {}
                  }
                />
              </div>
            ))}
          </Carousel>
        </div>
      )}

      <div
        className="container w-full md:max-w-3xl mx-auto mb-12 px-4 lg:px-0"
        style={
          !showbanner
            ? {
                marginTop: headerRef.current
                  ? headerRef.current.clientHeight + 32
                  : 74 + 32,
              }
            : { marginTop: 32 }
        }
      >
        {children}
      </div>

      <footer className="bg-white border-t border-gray-400 shadow">
        <div className="container max-w-4xl mx-auto flex py-8">
          <div className="w-full mx-auto flex flex-wrap">
            <div className="flex w-full">
              <div className="px-8">
                <p className="py-4 text-gray-600 text-sm text-center mb-2">
                  Специализированный центр компетенций «Молодые профессионалы
                  IT-лицея КФУ», 2016&mdash;2022 <br />
                  Разработка сайта, логотипа, оформления &mdash; участник
                  WorldSkills в компетенции «Веб-дизайн и разработка»{" "}
                  <a
                    href="//vk.com/ipetrovofficial"
                    target="_blank"
                    rel="noreferrer"
                    className="text-brand"
                  >
                    Петров Иван
                  </a>
                </p>
                <div className="grid gap-8 grid-cols-2 md:grid-cols-4 justify-items-center items-center h-18">
                  <img src={PartnerWS} className="max-h-12" alt="" />
                  <img src={PartnerWSRT} className="max-h-12" alt="" />
                  <img src={PartnerITL} className="max-h-9" alt="" />
                  <img src={PartnerIISDF} className="max-h-7" alt="" />
                </div>
              </div>
            </div>
          </div>
        </div>
      </footer>
    </>
  )
}

export default Layout
